import Vue from 'vue'
import Router from 'vue-router'
import GoodList from '../view/GoodList.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/GoodsList'
    },
    {
      path:'/GoodsList',
      component:GoodList
    }
  ]
})



